@import url(https://fonts.googleapis.com/css?family=Lato:400,600,700|Work+Sans:400,700,800&display=swap);
.nav-link {
  padding: 20px 15px !important;
  text-transform: uppercase !important;
  font-size: 14px !important;
  letter-spacing: 1px !important;
  font-weight: 700 !important;
  color: #000 !important;
}
.nav-up {
  top: -50px !important;
  transition: top 0.2s ease-out !important;
  position: sticky !important;
}
.navbar.solid {
  background-color: rgba(255, 255, 255, 0.5) !important;
  box-shadow: 0 0 4px grey !important;
}
.header-navigation {
  height: 50px;
  position: sticky;
  top: 0;

  z-index: 999;
}
a:hover {
  color: #f44336 !important;
  text-decoration: none;
  outline: none;
}
.nav-down {
  transition: top 0.1s ease-in !important;
}

.headingImg {
  max-width: 50%;
  position: absolute;
  left: auto;
  top: auto;
  right: -22%;
  bottom: 0%;
  max-width: 65%;
}
.shapesAccent {
  position: absolute;
  left: auto;
  top: auto;
  right: -20%;
  bottom: 0%;
  z-index: 0;
  width: 60%;
}
.heroContainer {
  position: relative;
  width: 100%;
  max-width: 1060px;
  margin-right: auto;
  margin-left: auto;
}
.heroSection {
  padding-right: 3%;
  padding-left: 3%;
  border-radius: 0;
  background-color: #ffff;
}
.heroBackground {
  position: relative;
  display: flex;
  width: 100%;
  min-height: 680px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 5%;
  padding-left: 5%;
  flex-direction: row;
  align-items: stretch;
  /* background-color: #f9f9fc; */
  background-color: #f9f9fc;
}

.heroContent {
  position: relative;
  z-index: 9;
  display: flex;
  height: 100%;
  max-width: 610px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.heroHeading {
  font-size: 84px;
  line-height: 90px;
  font-weight: 700;
  color: #333f63;
  font-family: Eudoxussans, sans-serif;
}
.heroSubheading {
  margin-top: 10px;
  color: #715c8c;
  height: 10%;
}
.heroSubheadingDiff {
  color: #838bc2;
}
@media screen and (max-width: 479px) {
  .heroBackground {
    padding-right: 3%;
    padding-left: 3%;
  }
  .heroHeading {
    font-size: 46px;
    line-height: 54px;
  }
  .heroSubheading {
    font-size: 12px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
}
@media screen and (max-width: 991px) {
  .headingImg {
    position: relative;
    max-width: 70%;
  }
  .heroBackground {
    padding-top: 60px;
    padding-bottom: 0px;
    flex-direction: column;
  }
  .heroSection {
    padding-bottom: 0px;
  }
  .heroContent {
    padding-bottom: 0px;
  }
  .heroHeading {
    font-size: 64px;
    line-height: 72px;
  }
  .heroSubheading {
    font-size: 32px;
    height: 100%;
  }
}
